<template>
  <div class="">
    <b-row class="">
      <b-col cols="12">
        <b-row>
          <div class="col-md-7">
            <label class="mb-2 mt-3" for="inline-form-input-name">Script</label>
            <b-card>
              <b-form>
                <textarea disabled :value="code" class="form-control mx-3" rows="15"></textarea>
              </b-form>
            </b-card>
          </div>
          <div class="col-md-5">
            <label class="mb-2 mt-3" for="inline-form-input-name">FilePicker</label>
            <div class="row">
              <div class="col-md-6">
                <button @click="loadFilePicker" size="sm" class="btn btn-block btn-primary">Get File</button>
              </div>
              <div class="col-md-6">
                <button @click="upload_file = !upload_file" size="sm" class="btn btn-block btn-primary">Upload File</button>
              </div>
            </div>
            <div v-if="upload_file">
              <div><label class="mb-2 mt-3" for="inline-form-input-name">Select File to Upload</label></div>
              <input type="file" id="file_data" @change="uploadFile($event)" />
            </div>
            <label class="mb-2 mt-3" for="inline-form-input-name">Result</label>
            <div class="row">
              <p v-if="!upload_file">{{ file_data }}</p>
              <div id="show_image" v-else></div>
              <p></p>
            </div>
          </div>
        </b-row>
      </b-col>
    </b-row>
    <div id="container"></div>
  </div>
</template>
<script>
  export default {
    name: "FilePicker",
    data() {
      return {
        code: "",
        file_data: "",
        upload_file: false,
      };
    },
    props: {
      api_key: String,
    },
    mounted() {
      this.code = `window._AppletIO.getFilePicker().getFile((event) => {console.log(event);})) window._AppletIO.getFilePicker().uploadFile({{file_data}})`;
    },
    methods: {
      uploadFile(event) {
        window._AppletIO.getFilePicker().uploadFile(event.target.files[0]);
        this.upload_file = false;
        this.file_data = "successfully uploaded";
      },

      loadFilePicker() {
        let vue = this;
        vue.file_data = "";
        vue.upload_file = false;
        window._AppletIO.getFilePicker().getFile((event) => {
          vue.convertImageURL(event);
        });
      },
      convertImageURL(blob) {
        var reader = new window.FileReader();
        reader.readAsDataURL(blob);
        reader.onload = function() {
          const img = document.createElement("img");
          img.src = reader.result;
          this.file_data = reader.result;
          document.getElementById("show_image").appendChild(img);
        };
      },
    },
  };
</script>
<style scoped></style>
